const formEl = document.querySelector('.questions__form');
const validate = new window.JustValidate(formEl);

export default function initValidate() {
  validate
    .addField('#name', [
      {
        rule: 'required',
        errorMessage: '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è',
      },
      {
        rule: 'minLength',
        value: 3,
        errorMessage: '–ò–º—è –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 3 —Å–∏–º–≤–æ–ª–∞',
      },
      {
        rule: 'maxLength',
        value: 20,
        errorMessage: '–ò–º—è –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∞–∫—Å–∏–º—É–º 20 —Å–∏–º–≤–æ–ª–æ–≤',
      },
    ])
    .addField('#email', [
      {
        rule: 'required',
        errorMessage: '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –≤–∞—à—É –ø–æ—á—Ç—É',
      },
      {
        rule: 'email',
        errorMessage: '–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –∞–¥—Ä–µ—Å',
      },
    ])
    .addField('#agree', [
      {
        rule: 'required',
        errorMessage: '–í—ã –¥–æ–ª–∂–Ω—ã —Å–æ–≥–ª–∞—Å–∏—Ç—å—Å—è –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –¥–∞–Ω–Ω—ã—Ö',
      },
    ])
    .onSuccess(async (event) => {
			event.preventDefault();

			const formData = new FormData(event.target);

			try {
				const response = await fetch('https://httpbin.org/post', {
					method: 'POST',
					body: formData,
				});

        formEl.reset();

				if (!response.ok) {
					throw new Error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –¥–∞–Ω–Ω—ã—Ö');
				}
				showModal('–ë–ª–∞–≥–æ–¥–∞—Ä–∏–º –∑–∞ –æ–±—Ä–∞—â–µ–Ω–∏–µ!');
			} catch (error) {
				showModal('–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ');
			}
		});
}

function showModal(title) {
	const modal = document.createElement('div');
	modal.className = 'message';
	modal.innerHTML = `
    <div class="message__content">
      <button class="message__close">&times;</button>
      <h2>${title}</h2>
    </div>
  `;

	document.body.appendChild(modal);

	modal.querySelector('.message__close').addEventListener('click', () => {
		modal.remove();
	});

	modal.addEventListener('click', (e) => {
		if (e.target === modal) {
			modal.remove();
		}
	});
}
